@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
/* ToDoInsert */
.ToDoInsert {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  padding: 60px;
}
.ToDoInsert > div {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: var(--light-black);
}
.ToDoInsert form {
  width: 100vw;
  padding: 4rem 1.4rem;
  flex-direction: row;
  justify-content: space-between;
  background-color: var(--light-white);
  border-radius: 6px;
  z-index: 3;
}
.ToDoInsert form input {
  background: none;
  outline: none;
  border: none;
  line-height: 1.5;
  font-size: 1rem;
  margin-left: 0.5rem;
}
.ToDoInsert button {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 1.4rem;
  color: var(--light-black);
}

:root {
  --black: #272727;
  --light-black: rgba(0, 0, 0, 0.4);
  --light-white: rgba(255, 255, 255, 0.8);
  --lighter-white: rgba(255, 255, 255, 0.4);
}

* {
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Display", "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* App */
.App {
  width: 100vw;
  height: 100vh;
  padding: 60px 60px 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #272727;
  color: var(--black);
}
.App__button {
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.4);
  color: var(--lighter-white);
  cursor: pointer;
  margin-top: 2rem;
}

.App__info {
  flex-direction: column;
}

/* TodoTime */
.TodoTime {
  color: white;
  text-align: center;
}
.TodoTime__time {
  font-size: 8rem;
  font-weight: 600;
  margin-bottom: 2rem;
}
.TodoTime__comment {
  padding-bottom: 20px;
  font-size: 1.4rem;
  font-weight: 500;
  margin-bottom: 40px;
  color: white;
  text-align: center;
}

/* TodoWeatherIcon */
.TodoWeatherIcon {
  text-align: end;
  font-size: 4rem;
  color: white;
  width: 26rem;
  height: 3rem;
}

/* TodoList */
.TodoList {
  width: 100%;
}

/* TodoItem */
.TodoItem {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1.4rem;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.4);
  background-color: var(--lighter-white);
}
.TodoItem__content {
}
.TodoItem__content.checked {
  text-decoration: line-through;
  color: var(--light-gray);
}
.TodoItem__icon {
  margin-right: 0.5rem;
  cursor: pointer;
}
.TodoItem__text {
  cursor: pointer;
}

/* Background */
.Background {
  width: 100vw;
  height: 100vh;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  background-size: cover;
}

